.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.setbg{
  background-image: url('https://3.bp.blogspot.com/-tJU0LDSKMIE/UtlWV9EwhBI/AAAAAAAAF3Y/WTNhbcVjZeI/s1600/orange-web-background.jpg');
  background-size: cover;
}
.divpad{
  margin-top: 50px;
}
.headerdiv{
  background-color: #E91E63;
}
.font_header{
  font-family: sans-serif;
  text-rendering: optimizeLegibility;
  font-style: italic;
  color:#fff;
}
.divpadmore{
  padding-left:60px;
  margin-top: 60px;
}
.transform{
  -webkit-transform: translateX(-100%);
      -ms-transform: translateX(-100%);
          transform: translateX(-100%);
}
.imgContent{
  width:"60%";
  height:"60%";
  border-radius:50%;
  padding:30px;
}
.textjustify{
  text-align: justify;
}
:root {
    --space: 1.5em 0;
    --space: 2em 0;
}

.Site {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    min-height: 100vh;
}

.Site-content {
    -ms-flex: 1 0 auto;
        flex: 1 0 auto;
    padding: var(--space) var(--space) 0;
    width: 100%;
}

.Site-content:after {
    content: '\00a0';
    display: block;
    margin-top: var(--space);
    height: 0;
    visibility: hidden;
}
a.active { color: #f00 }
.center-row{
      margin-top:100px;
}

#user-box {
  display: inline-block;
  width: 40%;
  margin: 20px;
  background-color: pink;
  padding-top: 15px;
}
.user-pic {
  height: 100px;
  width: 100px;
  margin-top: 5px;

}
#user-info {
  font-size: 14px;
  text-align: center;
}



